@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:ital,wght@0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.searchnav {
    background: black;
    color: white;
    margin: 0;
    padding: 0.6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.searchnav h1 {
    margin: 0;
}

.search-input {
    margin: 10px 0;
    width: 30vw;
    height: 35px;
    border-radius: 15px;
    border: none;
}

.search-input:active {
    border: none;
}
.main {
    margin: 2rem;
    padding: 0 4rem;
}

.main img {
    width: 250px;
    height: 200px;
    margin: 10px;
    cursor: pointer;
}

.image--container {
    margin-top: 20px;
}
.popup--back {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 50;
    bottom: 0;
}

.popup--content {
    width: 50vw;
    height: auto;
    background-color: white;
    border-radius: 8px;
    position: relative;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.popup--close {
    font-weight: bold;
    font-size: 40px;
    position: absolute;
    right: 20px;
    cursor: pointer;
    color: red;
}

.image--container {
    margin-top: 2rem;
}
body {
    margin: 0;
    padding: 0;
}

* {
    font-family: "Noto Serif", serif;
}
